import { mapMutations } from 'vuex'

export default {
    auth: false,
    data(){ 
        return {
            auth:{
                username: null,
                password: null
            },

            snackbar: false,
            text: "",
            timeout: 2000,
            dialog: false,
            isLoading: false
        }
 

    },

    mounted() {

        if(this.$auth.loggedIn){
            this.$router.push('/student');


        }


    },
    methods:{
        ...mapMutations(['SET_IS_AUTH', 'SET_API_TOKEN']),

        login(){
                //  this.$notify({
                //         'group': 'foo',
                //         'title': 'Kontol-kontol Anjing',
                //         'text': 'Terbuat Dari Nasi',

                //     }),
              this.isLoading = true,
                 
                this.$auth.loginWith('local', {
                    
                    data:{
                        username: this.auth.username,
                        password: this.auth.password
                    },
            
                }).then((response) => {
                    // this.$toast.show('Logging in...')
                    console.log(response.data);
                    this.SET_IS_AUTH(true);
                    // this.SET_API_TOKEN(response.data.access_token);
                    this.$auth.setUser(response.data);
                    this.$auth.setUserToken(response.data.access_token);
                    this.SET_API_TOKEN(this.$auth.$storage._state["_token.local"]);
                    //  this.$auth.fetchUser();
                    console.log(this.$auth);
                    this.isLoading= false;
                  
              
                   
                    this.$router.push('/student');
                   
                    // console.log("Test !");
                    // console.log(this.$auth.loggedIn);
                    // this.$toast.success('Successfully authenticated')

                    // this.$auth.setUserToken(response.data.access_token)
                    // .then(() => this.$toast.success('User set!'))

                }).catch((error) =>{
                    // this.isLoading = false;

                    //   this.$notify({
                    //     'group': 'foo',
                    //     'title': 'Kontol-kontol Anjing',
                    //     'text': 'Terbuat Dari Nasi',

                    // })
                    this.text = error.response.data.message;
                    this.snackbar = true;
                    this.isLoading= false
                    // this.isLoaded = true;
                })

             
        }

    }
}
